.category-name-button {
    border: none;
    background: transparent;
    border-radius: 20px;
    font-size: 14px;
    font: normal normal medium 14px/19px Roboto;
    color: #000000;
    height: 100%;
    width: 100%;
    margin: 5px;
    padding: 10px;
    cursor: pointer;
    background: #cfd4d3 0% 0% no-repeat padding-box;
}

